/* General Reset */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Navbar */
.navbar {
  background-color: #333;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  padding: 10px 0;
  z-index: 1000;
}

.navbar ul {
  list-style: none;
  display: flex;
  justify-content: center;
}

.navbar ul li {
  margin: 0 20px;
}

.navbar ul li a {
  color: white;
  text-decoration: none;
  font-size: 18px;
}

.navbar ul li a:hover {
  text-decoration: underline;
}

/* Home Section */
.home {
  background-color: #f5f5f5;
  padding: 100px 20px;
  text-align: center;
}

.home h1 {
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.home p {
  font-size: 1.2rem;
  margin-bottom: 20px;
}

.home button {
  font-size: 18px;
  padding: 10px 20px;
  background-color: #007BFF;
  color: white;
  border: none;
  cursor: pointer;
}

.home button:hover {
  background-color: #0056b3;
}

/* About Section */
.about {
  background-color: #f0f0f0;
  padding: 60px 20px;
  width: 100%;
  text-align: center;
}

.about h2 {
  font-size: 2rem;
  margin-bottom: 20px;
}

.about p {
  font-size: 1.2rem;
  line-height: 1.6;
}

/* Skills Section */
.skills {
  background-color: #ffffff;
  padding: 60px 20px;
  width: 100%;
  text-align: center;
}

.skills h2 {
  font-size: 2rem;
  margin-bottom: 20px;
}

.skills ul {
  list-style-type: none;
  padding: 0;
}

.skills ul li {
  font-size: 1.2rem;
  margin-bottom: 10px;
}

/* Contact Section */
.contact {
  background-color: #e9ecef;
  padding: 60px 20px;
  width: 100%;
  text-align: center;
}

.contact h2 {
  font-size: 2rem;
  margin-bottom: 20px;
}

.contact p {
  font-size: 1.2rem;
  margin-bottom: 20px;
}

.contact button {
  font-size: 18px;
  padding: 10px 20px;
  background-color: #007BFF;
  color: white;
  border: none;
  cursor: pointer;
}

.contact button:hover {
  background-color: #0056b3;
}

/* Footer */
.footer {
  background-color: #333;
  color: white;
  padding: 20px 0;
  width: 100%;
  text-align: center;
}

.footer p {
  margin: 0;
}
